body {
  font-family: 'Roboto', sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Roboto', sans-serif;
  font-weight: 700; /* Adjust font weight for headings */
 
}
h5{
  margin-bottom: 30px;
}
/* Example: Set font size and line height for body text */
p {
  font-size: 16px;
  line-height: 1.5;

}

/* Example: Set font size and styles for headings */
h1 {
  font-size: 36px;
}

h2 {
  font-size: 24px;
}
/* The actual timeline (the vertical ruler) */

.main-timeline {
  position: relative;
}

/* The actual timeline (the vertical ruler) */
.main-timeline::after {
  content: "";
  position: absolute;
  width: 6px;
  background-color: #939597;
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -3px;
}

.extra-black-bg {
  background-color: #000; /* Use a black color code, or adjust it as needed */
  color :white;
}

/* Container around content */
.timeline {
  position: relative;
  background-color: inherit;
  width: 32%;
  border-radius: 8px;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  margin-bottom: 20px; /* Add space between timeline entries */
}

.timeline:hover {
  transform: translateY(-5px);
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);
}

/* The circles on the timeline */
.timeline::after {
  content: "";
  position: absolute;
  width: 25px;
  height: 25px;
  right: -13px;
  background-color: #939597;
  border: 5px solid #dc3545;
  top: 15px;
  border-radius: 50%;
  z-index: 1;
}
.btn-footer {
  color: #dc3545;
}
.hero-text {
  color: hsl(354, 76%, 60%);
}



.technologies {
