ine::after {\n  content: \"\";\n  position: absolute;\n  width: 25px;\n  height: 25px;\n  right: -13px;\n  background-color: #939597;\n  border: 5px solid #dc3545;\n  top: 15px;\n  border-radius: 50%;\n  z-index: 1;\n}\n.btn-footer {\n  color: #dc3545;\n}\n.hero-text {\n  color: hsl(354, 76%, 60%);\n}\n\n\n\n.technologies {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 4px;\n  margin-top: 10px;\n}\n\n.technology {\n  background-color: #f0f0f0;\n  padding: 4px 8px;\n  border-radius: 4px;\n  font-size: 12px;\n  border: 1px solid #000;\n  color: #000;\n  margin-bottom: 10px;\n}\n\n\n.active {\n  color: #dc3545; /* Change to your desired active link color */\n  font-weight: bold; /* Change to your desired active link font weight */\n}\n.nav-link {\n  color: white;\n  text-decoration: none; /* Remove underline */\n  transition: color 0.2s; /* Smooth color transition on hover */\n}\n\n/* Style for the link when it's active or on hover (pink color) */\n.nav-link.active\n{ color: #dc3545;\n\n}\n.nav-link:hover {\n  color: #007bff;\n}\n/* You can customize the hover effect and link styles further as needed */\n\n/* Place the container to the left */\n.left {\n  padding: 0px 40px 20px 0px;\n  left: 18%;\n}\n\n/* Place the container to the right */\n.right {\n  padding: 0px 0px 20px 40px;\n  left: 50%;\n}\n\n/* Add arrows to the left container (pointing right) */\n.left::before {\n  content: \" \";\n  position: absolute;\n  top: 18px;\n  z-index: 1;\n  right: 30px;\n  border: medium solid white;\n  border-width: 10px 0 10px 10px;\n  border-color: transparent transparent transparent white;\n}\n\n/* Add arrows to the right container (pointing left) */\n.right::before {\n  content: \" \";\n  position: absolute;\n  top: 18px;\n  z-index: 1;\n  left: 30px;\n  border: medium solid white;\n  border-width: 10px 10px 10px 0;\n  border-color: transparent white transparent transparent;\n}\n\n/* Fix the circle for containers on the right side */\n.right::after {\n  left: -12px;\n}\n\n/* Media queries - Responsive timeline on screens less than 600px wide */\n@media screen and (max-width: 600px) {\n  /* Place the timeline to the left */\n  .main-timeline::after {\n    left: 31px;\n  }\n\n  /* Full-width containers */\n  .timeline {\n    width: 100%;\n    padding-left: 70px;\n    padding-right: 25px;\n  }\n\n  /* Make sure that all arrows are pointing leftwards */\n  .timeline::before {\n    left: 60px;\n    border: medium solid white;\n    border-width: 10px 10px 10px 0;\n    border-color: transparent white transparent transparent;\n  }\n\n  /* Make sure all circles are at the same spot */\n  .left::after,\n  .right::after {\n    left: 18px;\n  }\n\n  .left::before {\n    right: auto;\n  }\n  .left {\n    left: 0%;\n  }\n  /* Make all right containers behave like the left ones */\n  .right {\n    left: 0%;\n  }\n}\n/* Timeline card styling */\n.timeline-card {\n  background-color: #fff;\n  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);\n  padding: 15px;\n  border-radius: 8px;\n  transition: transform 0.2s ease, box-shadow 0.2s ease;\n  margin: 5px 0; /* Add space between cards */\n}\n\n.timeline-card:hover {\n  transform: translateY(-5px);\n  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);\n    transform: translateY(-15px); /* Move tech item up slightly on hover */\n  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2); /* Box shadow on hover */\n    background-color: black; /* Background color for tech items */\n  border: 1px solid black; \n color:white\n}\n\n/* Customize text styles within the card */\n.timeline-designation {\n  font-size: 20px;\n  color: #007bff; /* Blue color for designation */\n  margin-bottom: 8px;\n}\n\n.timeline-description {\n  font-size: 16px;\n  line-height: 1.4;\n  margin-top: 10px;\n  color: #333; /* Change the color to match your design */\n}\n\n.timeline-company {\n  font-size: 18px;\n  font-weight: bold;\n\n}\n\n\n.timeline-year {\n  font-size: 16px;\n  color: #6c757d; /* Gray color for year */\n}\n\n\n/* Tech Item Styles */\n.tech-item {\n  background-color: #fff; /* Background color for tech items */\n  border: 1px solid #ddd; /* Border around tech items */\n  padding: 30px; /* Spacing inside tech items */\n  border-radius: 5px; /* Rounded corners for tech items */\n  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* Box shadow for tech items */\n  transition: transform 0.2s, box-shadow 0.2s; /* Transition effect on hover */\n  color: black;\n  margin-bottom: 40px; /* Add space in the bottom */\n}\n\n.job{\n  color:black;\n}\n\n.job:hover{\n  color: white;\n}\n.tech-item:hover {\n  transform: translateY(-25px); /* Move tech item up slightly on hover */\n  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2); /* Box shadow on hover */\n    background-color: black; /* Background color for tech items */\n  border: 1px solid black; \n  color: white;\n}\n\n/* Tech Icon Styles */\n.tech-icon {\n  color: #007bff; /* Color of tech icons */\n  width: 50px; /* Adjust the size as needed */\n  height: 50px;\n}\n\n/* Tech Stack Titles */\nh5 {\n  font-size: 1.25rem; /* Font size for tech stack titles */\n  margin-top: 1rem; /* Spacing above titles */\n}\n\n.text-color {\n  color: white;\n}\n\n.custom-text{\n  margin: 0 10px;\n}\n/* Tech Stack Description */\n.text-muted {\n  font-size: 1rem; /* Font size for tech stack descriptions */\n  margin-top: 0.5rem; /* Spacing above descriptions */\n}\n.icon-grid {\n  display: grid;\n  grid-template-columns: repeat(5, 1fr); /* Display 5 columns in a row */\n  gap: 20px; /* Adjust the gap between icons and text */\n  text-align: center;\n  align-items: center;\n  justify-items: center;\n}\n\n.icon-grid-1 {\n  display: grid;\n  grid-template-columns: repeat(4, 1fr); /* Display 5 columns in a row */\n  gap: 10px; /* Adjust the gap between icons and text */\n  text-align: center;\n  align-items: center;\n  justify-items: center;\n}\n\n.icon-item img {\n  max-width: 64px; /* Adjust the max width of the icon */\n  height: 40px;\n}\n\n/* Add this CSS to your stylesheet or create a new CSS file */\n\n.cta {\n  /* Background color for the CTA section */\n  /* Text color */\n  text-align: center;\n  margin-top: 30px; /* Add space from the top */\n  margin-bottom: 30px; /* Add space from the bottom */\n  max-width: 600px; /* Adjust the maximum width as needed */\n  margin-left: auto;\n  margin-right: auto;\n}\n\n.cta-title {\n  font-size: 32px;\n  font-weight: bold;\n  margin-bottom: 23px;\n}\n\n.cta-text {\n  font-size: 21px;\n  font-style: oblique;\n  margin-bottom: 30px;\n  text-align: center;\n\n  word-wrap: break-word;\n}\n/* Add this CSS to your component's stylesheet or a global stylesheet */\n.custom-title {\n  /* Apply your desired styles here */\n  font-size: 24px; /* Adjust the font size as needed */\n  color: black; /* Set the color to #dc3545 */\n  position: relative; /* Position relative for pseudo-element */\n  font-weight: bold;\n  margin-top: 20px;\n}\n\n.custom-title::before {\n  content: \"\"; /* Create a pseudo-element */\n  position: absolute;\n  bottom: -5px; /* Adjust the distance of the underline */\n  left: 0;\n  width: 6%;\n  height: 2px; /* Adjust the height of the underline */\n  background-color: #dc3545; /* Set the underline color */\n}\n\n.custom-title-1{\n  /* Apply your desired styles here */\n /* Adjust the font size as needed */\n  color: white; /* Set the color to #dc3545 */\n  position: relative; /* Position relative for pseudo-element */\n \n  margin-top: 20px;\n}\n\n.custom-title-1::before {\n  content: \"\"; /* Create a pseudo-element */\n  position: absolute;\n  bottom: -5px; /* Adjust the distance of the underline */\n  left: 0;\n  width: 8%;\n  height: 2px; /* Adjust the height of the underline */\n  background-color: #dc3545; /* Set the underline color */\n}\n\n.cta-btn {\n  /* Button text color */\n  font-size: 20px;\n  padding: 15px 30px; /* Adjust the padding as needed */\n  text-decoration: none;\n  border-radius: 15px;\n  transition: background-color 0.3s, color 0.3s;\n  \n}\n\n.cta-btn:hover {\n  background-color: #dc3545; /* Button background color on hover */\n  color: #fff; /* Button text color on hover */\n   transform: translateY(-5px);\n  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);\n  \n}\n.custom-margin {\n  margin: 0 10px; /* Adjust the margin value as needed */\n}\n.project-card {\n  max-width: 300px;\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\n  transition: transform 0.2s, box-shadow 0.2s; /* Add transition to transform and box-shadow */\n  border: 1px solid black;\n  background-color: white;\n  text-align: center;\n  position: relative; /* Add position relative for pseudo-element */\n}\n\n\n\n.project-card:hover {\n  transform: translateY(-25px);\n  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);\n  \n\n}\n\n\n\n.project-link {\n  display: block;\n}\n\n.project-card-image {\n  max-height: 150px;\n  max-width: 300px;\n}\n\n.project-card-title {\n  font-size: 16px;\n  font-weight: bold;\n  margin: 10px 0;\n}\n\n.blog-meta {\n  font-size: 0.9rem;\n  color: gray;\n  margin-top: 5px;\n}\n\n.author-name {\n  font-weight: bold;\n}\n\n.blog-date {\n  margin-left: 5px;\n}\n\n\n"],"sourceRoot":""}]);
